<script>
export default {
  data() {
    return {
      carts: [],
      cartModal: '',
      tempData: '',
    };
  },
  methods: {
    getCarts() {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
      this.$http.get(api).then((res) => {
        this.carts = res.data.data;
      });
    },
  },
  mounted() {
    this.getCarts();
  },
};
</script>
